import React, { createContext, useState, useReducer, useEffect } from "react";
import actionTypes from "../actions/types";
import postReducer, { INITIAL_STATE } from "../reducers/postReducer";
import axios from "axios";

export const PostContext = createContext(INITIAL_STATE);

const PostContextProvider = (props) => {
  const [state, dispatch] = useReducer(postReducer, INITIAL_STATE);
  const { postDetails, loading, error } = state;
  const editpost = (post) => {
    dispatch({ type: actionTypes.EDIT_POST, data: post });
  };

  const removepost = (id) => {
    dispatch({ type: actionTypes.REMOVE_POST, data: id });
  };

  const getpost = (post) => {
    dispatch({ type: actionTypes.GET_POST, data: post });
  };

  const searchpost = (post) => {
    dispatch({ type: actionTypes.SEARCH_POST, data: post });
  };

  const updatepost = (post) => {
    dispatch({ type: actionTypes.UPDATE_POST, data: post });
  };

  useEffect(() => {
    dispatch({ type: actionTypes.SUCCESS_CALL_API });
    axios
      .get("/post")
      .then((response) => {
        if (response.status == 200) {
          dispatch({ type: actionTypes.GET_POST, data: response.data });
          return;
        }
      })
      .catch((error) => {
        dispatch({ type: actionTypes.ERROR_CALL_API, error: error });
      });
  }, []);

  return (
    <PostContext.Provider
      value={{
        post: state.postDetails,
        getpost,
        searchpost,
        updatepost,
        removepost,
        editpost,
      }}
    >
      {props.children}
    </PostContext.Provider>
  );
};

export default PostContextProvider;
