import React, { useContext, useState } from "react";
import { withStyles } from "@material-ui/core/styles";
import AutoSearch from "./AutoSearch";
import {
  Grid,
  Button,
  AppBar,
  Card,
  CardContent,
  CardActions,
  Toolbar,
  makeStyles,
  Typography,
  Paper,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  TextField,
  Divider,
} from "@material-ui/core";
import { PostContext } from "../context/PostContext";

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  paper: {
    padding: theme.spacing(2),
    textAlign: "center",
  },
  title: {
    padding: theme.spacing(2),
    marginBottom: 16,
    fontSize: 14,
  },
  card: {
    maxWidth: 300,
    margin: "auto",
    transition: "0.3s",
    boxShadow: "0 8px 40px -12px rgba(0,0,0,0.3)",
    "&:hover": {
      boxShadow: "0 16px 70px -12.125px rgba(0,0,0,0.3)",
    },
  },
  media: {
    paddingTop: "56.25%",
  },
  content: {
    textAlign: "left",
    padding: theme.spacing(3),
  },
  divider: {
    margin: `${theme.spacing (3)}px 0`,
  },
  heading: {
    fontWeight: "bold",
  },
  subheading: {
    lineHeight: 1.8,
  },
}));
const PostDetails = () => {
  const classes = useStyles();
  const { post, searchpost, editpost, removepost, updatepost, getpost } =
    useContext(PostContext);
  const [selecteDelete, setSelecteDelete] = useState([]);
  const [show, setShow] = useState(false);
  const [open, setOpen] = useState(false);

  const [selected, setSelected] = useState({
    id: null,
    title: "",
    post: "",
  });
  const handleEdit = (post) => {
    setSelected(post);
    setShow(true);
    setOpen(true);
  };
  const handleClose = () => setShow(false);

  const handleDelete = (id) => {
    setSelecteDelete(id);
    removepost(id);
  };

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setSelected({ ...selected, [name]: value });
  };

  const handleUpdate = () => {
    updatepost(selected);
    setShow(false);
  };

  return (
    <div className={classes.root}>
      <Grid container spacing={3}>
        <Grid item xs={12}>
            {" "}
            <AutoSearch />
        </Grid>
      </Grid>

      <Grid container spacing={3}>
        
        {post?.length > 0
          ? post?.map((post) => (
              <Grid item xs={3} key={post.id}>
                {" "}
                <Card className={classes.card} key={post.id}>
                  <CardContent className={classes.content}>
                    <CardContent>
                      <Typography gutterBottom variant="h5" component="div">
                        Title
                      </Typography>
                      <Typography color="secondary">{post.title}</Typography>
                      <Divider className={classes.divider}  />
                      <Typography gutterBottom variant="h5" component="div">
                        Body
                      </Typography>
                      <Typography variant="body2" color="primary">
                        {post.body}
                      </Typography>
                    </CardContent>
                    <CardActions>
                      <Button
                        variant="contained"
                        color="primary"
                        onClick={() => handleEdit(post)}
                      >
                        Edit
                      </Button>
                      <Button
                        variant="contained"
                        color="secondary"
                        onClick={() => handleDelete(post.id)}
                      >
                        DELETE
                      </Button>
                    </CardActions>
                  </CardContent>
                </Card>
              </Grid>
            ))
          : ""}

        {show ? (
          <Dialog open={show}>
            <DialogTitle>EditForm</DialogTitle>
            <DialogContent>
              <Grid container justify="center" alignItems="center" spacing={2}>
                <Grid item xs={12}>
                  <TextField
                    id="outlined-basic"
                    label="Title"
                    type="text"
                    name="title"
                    autoComplete="title"
                    variant="outlined"
                    onChange={handleInputChange}
                    value={selected.title}
                    fullWidth
                  />
                </Grid>
                <Grid item xs={12}>
                  <TextField
                    id="outlined-basic"
                    label="Body"
                    type="text"
                    name="body"
                    autoComplete="title"
                    variant="outlined"
                    onChange={handleInputChange}
                    value={selected.body}
                    fullWidth
                  />
                </Grid>
              </Grid>
            </DialogContent>
            <DialogActions>
              <Button
                color="secondary"
                variant="contained"
                onClick={handleClose}
              >
                Close
              </Button>
              <Button
                variant="contained"
                color="primary"
                onClick={handleUpdate}
              >
                Update
              </Button>
            </DialogActions>
          </Dialog>
        ) : (
          ""
        )}
      </Grid>
    </div>
  );
};

export default PostDetails;
